import React from 'react'
import BigNumber from 'bignumber.js'
import { BIG_ZERO } from 'utils/bigNumber'
import { Flex, Text, TooltipText, useTooltip } from '@pancakeswap/uikit'
import { Pool } from 'state/types'
import { useTranslation } from 'contexts/Localization'
import { useWeb3React } from '@web3-react/core'
import useWithdrawClamdownTimer from 'hooks/useWithdrawClamdownTimer'
import { useCakeVault } from 'state/hooks'
import WithdrawalFeeTimer from './WithdrawalFeeTimer'

interface ClamdownCountdownRowProps {
  pool: Pool
  pendingWithdraw:BigNumber,
  lastSubmitTime:BigNumber,
  hasPendingWithdraw:boolean,
  inPendingProcess:boolean,
  secondsRemaining:number
}
const ClamdownCountdownRow : React.FC<ClamdownCountdownRowProps> = ({ 
    pool,pendingWithdraw,lastSubmitTime,hasPendingWithdraw,inPendingProcess,secondsRemaining}) => {
  const { t } = useTranslation()
  const { account } = useWeb3React()

  const {userData} = pool;
  
  // const secondsRemaining = lastSubmitTime.toNumber() +72*3600 - Math.round(Date.now()/1000)
  // const submitTimePassed =  secondsRemaining<0
  const { targetRef, tooltip, tooltipVisible } = useTooltip(
    <>
      <Text bold mb="4px">
        {t('Unstaking and withdraw rules:')}
      </Text>
      <Text>
        {t(
          'UnStaked token can only withdraw after 72 hours of last unstaking action, during this your token is in pending withdraw status, you can pushback them to stake, or wait for 72 hours',
        )}
      </Text>
    </>,
    { placement: 'bottom-start' },
  )

  // The user has made a deposit, but has no fee
  const noFeeToPay = !inPendingProcess && hasPendingWithdraw

  // Show the timer if a user is connected, has deposited, and has an unstaking fee
  const shouldShowTimer = account && hasPendingWithdraw && inPendingProcess

  const getRowText = () => {
    if (noFeeToPay) {
      return t('Free to withdraw').toLowerCase()
    }
    if (shouldShowTimer) {
      return t('Withdraw avaliable until')
    }
    return t('72 hours of Unstaking calmdown time')
  }

  return (
    <Flex alignItems="center" justifyContent="space-between">
      {tooltipVisible && tooltip}
      <TooltipText ref={targetRef} small>
        {getRowText()}
      </TooltipText>
      {shouldShowTimer && <WithdrawalFeeTimer secondsRemaining={secondsRemaining} />}
    </Flex>
  )
}

export default ClamdownCountdownRow
