import React from 'react'
import { Flex, Text, Button, IconButton, AddIcon,ArrowUpIcon,ArrowDownIcon,RefreshIcon, MinusIcon, useModal, Skeleton, useTooltip } from '@pancakeswap/uikit'
import BigNumber from 'bignumber.js'
import { useTranslation } from 'contexts/Localization'
import { getBalanceNumber } from 'utils/formatBalance'
import { Pool } from 'state/types'
import { useBusdPriceFromToken } from 'state/hooks'
import Balance from 'components/Balance'
import NotEnoughTokensModal from '../Modals/NotEnoughTokensModal'
import WithdrawConfirmModal from '../Modals/WithdrawConfirmModal'

interface WithdrawActionsProps {
  pool: Pool
  stakingTokenBalance: BigNumber
  pendingBalance: BigNumber
  inPendingProcess:boolean
  isBnbPool: boolean
  hasPending: ConstrainBoolean
  isLoading?: boolean
}

const WithdrawAction: React.FC<WithdrawActionsProps> = ({
  pool,
  stakingTokenBalance,
  pendingBalance,
  isBnbPool,
  hasPending,
  inPendingProcess,
  isLoading = false,
}) => {
  const { stakingToken, stakingLimit, isFinished, userData } = pool
  const { t } = useTranslation()
  const stakedTokenBalance = getBalanceNumber(pendingBalance, stakingToken.decimals)
  const stakingTokenPrice = useBusdPriceFromToken(stakingToken.symbol)
  const stakingTokenPriceAsNumber = stakingTokenPrice && stakingTokenPrice.gt(0) ? stakingTokenPrice.toNumber() : 0
  const stakedTokenDollarBalance = getBalanceNumber(
    pendingBalance.multipliedBy(stakingTokenPriceAsNumber),
    stakingToken.decimals,
  )

  const [onPresentTokenRequired] = useModal(<NotEnoughTokensModal tokenSymbol={stakingToken.symbol} />)
  const TooltipComponent = () => (
    <>
      <Text mb="16px">{t('Rule1')}</Text>
      <Text mb="16px">
        {t(
          'UnStaked token can only withdraw after 72 hours of last unstaking action, during this your token is in pending withdraw status, you can pushback them to stake, or wait for 72 hours.',
        )}
      </Text>
      <Text style={{ fontWeight: 'bold' }}>
        {t('Rule3')}
      </Text>
    </>
  )
  const [onPresentStake] = useModal(
    <WithdrawConfirmModal
      pool ={pool}
      actionToWithdraw={false}
      pendingAmount = {pendingBalance}
      TooltipComponent={TooltipComponent}
    />,
  )

  const [onPresentUnstake] = useModal(
    <WithdrawConfirmModal
      pool ={pool}
      actionToWithdraw
      pendingAmount = {pendingBalance}
      TooltipComponent={TooltipComponent}
    />,
  )

  const { targetRef, tooltip, tooltipVisible } = useTooltip(
    t('You’ve already staked the maximum amount you can stake in this pool!'),
    { placement: 'bottom' },
  )

  // const reachStakingLimit = stakingLimit.gt(0) && userData.pendingBalance.gte(stakingLimit)
  
  const renderStakeAction = () => {
    return hasPending ? (
      <Flex justifyContent="space-between" alignItems="center">
        <Flex flexDirection="column">
          <>
            <Balance bold fontSize="20px" decimals={3} value={stakedTokenBalance} />
            {stakingTokenPriceAsNumber !== 0 && (
              <Text fontSize="12px" color="textSubtle">
                <Balance
                  fontSize="12px"
                  color="textSubtle"
                  decimals={2}
                  value={stakedTokenDollarBalance}
                  prefix="~"
                  unit=" USD"
                />
              </Text>
            )}
          </>
        </Flex>
        <Flex>

          <IconButton variant="secondary" onClick={onPresentUnstake} mr="6px" disabled={!inPendingProcess}>
            <ArrowDownIcon color="primary" width="24px" />
          </IconButton>
          <IconButton
              variant="secondary"
              onClick={stakingTokenBalance.gt(0) ? onPresentStake : onPresentTokenRequired}
              disabled={isFinished}
            >
              <RefreshIcon color="primary" width="24px" height="24px" />
          </IconButton>
        </Flex>
        {tooltipVisible && tooltip}
      </Flex>
    ) : (
      <Button disabled={isFinished} onClick={stakingTokenBalance.gt(0) ? onPresentStake : onPresentTokenRequired}>
        {t('Stake')}
      </Button>
    )
  }

  return <Flex flexDirection="column">{isLoading ? <Skeleton width="100%" height="52px" /> : renderStakeAction()}</Flex>
}

export default WithdrawAction
