import BigNumber from 'bignumber.js'
import { useEffect, useState } from 'react'

const useWithdrawClamdownTimer = (lastSubmit: number, pendingAmount: BigNumber, clamdownPeriod = 259200) => {
  const [secondsRemaining, setSecondsRemaining] = useState(null)
  const [inPendingProcess, setInPendingProcess] = useState(false)
  const [currentSeconds, setCurrentSeconds] = useState(Math.floor(Date.now() / 1000))

  useEffect(() => {
    const feeEndTime = lastSubmit + clamdownPeriod
    const secondsRemainingCalc = feeEndTime - currentSeconds
    const doesInpendingProcess = pendingAmount.gt(0) && secondsRemainingCalc > 0

    const tick = () => {
      setCurrentSeconds((prevSeconds) => prevSeconds + 1)
    }
    const timerInterval = setInterval(() => tick(), 1000)
    if (doesInpendingProcess) {
      setSecondsRemaining(secondsRemainingCalc)
      setInPendingProcess(true)
    } else {
      setInPendingProcess(false)
      clearInterval(timerInterval)
    }

    return () => clearInterval(timerInterval)
  }, [lastSubmit, clamdownPeriod, setSecondsRemaining, currentSeconds, pendingAmount])

  return { inPendingProcess, secondsRemaining }
}

export default useWithdrawClamdownTimer
