import React, { useState } from 'react'
import BigNumber from 'bignumber.js'
import { useWeb3React } from '@web3-react/core'
import { DEFAULT_GAS } from 'config'
import styled from 'styled-components'
import { Modal, Text, Flex, Button, HelpIcon, AutoRenewIcon, useTooltip } from '@pancakeswap/uikit'
import { getBalanceNumber } from 'utils/formatBalance'
import { useStakingChefContract } from 'hooks/useContract'
import { Pool } from 'state/types'
import useTheme from 'hooks/useTheme'
import useToast from 'hooks/useToast'
import { useTranslation } from 'contexts/Localization'
import UnlockButton from 'components/UnlockButton'
import Balance from 'components/Balance'

interface WithdrawConfirmModalProps {
  pool: Pool
  actionToWithdraw:boolean
  pendingAmount:BigNumber
  onDismiss?: () => void
  TooltipComponent: React.ElementType
}

const Divider = styled.div`
  background-color: ${({ theme }) => theme.colors.backgroundDisabled};
  height: 1px;
  margin: 16px auto;
  width: 100%;
`

const WithdrawConfirmModal: React.FC<WithdrawConfirmModalProps> = ({
  pool,
  actionToWithdraw,
  pendingAmount,
  onDismiss,
  TooltipComponent,
}) => {
  const { t } = useTranslation()
  const { account } = useWeb3React()
  const { theme } = useTheme()
  const { toastError, toastSuccess } = useToast()
  const vaultContract = useStakingChefContract()
  const [pendingTx, setPendingTx] = useState(false)
  const {stakingToken} = pool
  const { targetRef, tooltip, tooltipVisible } = useTooltip(<TooltipComponent />, {
    placement: 'bottom',
    tooltipPadding: { right: 15 },
  })

  const handleWithdrawClick = async () => {
    try{
      const gasLimit = await vaultContract.methods
      .claimPendingWithdraw(pool.sousId,pendingAmount).estimateGas({from:account})
      vaultContract.methods
        .claimPendingWithdraw(pool.sousId,pendingAmount)
        .send({ from: account, gas: new BigNumber(gasLimit).times(120).idiv(100) })
        .on('sending', () => {
          setPendingTx(true)
        })
        .on('receipt', () => {
          toastSuccess(t('Pending token withdrawed!'), t(`${stakingToken.symbol} has been sent to your wallet.`))
          setPendingTx(false)
          onDismiss()
        })
        .on('error', (error) => {
          console.error(error)
          toastError(
            t('Could not be withdrawed'),
            t('There may be an issue with your transaction, or your clamdown period did not end.'),
          )
          setPendingTx(false)
          onDismiss()
        })
    }catch(e){
      toastError(
        t('Could not be withdrawed'),
        t('There may be an issue with your transaction, or your clamdown period did not end.'),
      )
      setPendingTx(false)
      onDismiss()
    }
  }
  const handleDepositBackClick = async ()=>{
    const gasLimit = await vaultContract.methods
      .pushBackPending(pool.sousId).estimateGas({from:account})
      // .pushBackPending(pool.sousId,pendingAmount).estimateGas()
    console.log(`poolid:${pool.sousId},gaslimit:${gasLimit}`)
    vaultContract.methods
      .pushBackPending(pool.sousId)
      // .pushBackPending(pool.sousId,pendingAmount)
      .send({ from: account, gas: new BigNumber(gasLimit).times(120).idiv(100) })
      .on('sending', () => {
        setPendingTx(true)
      })
      .on('receipt', () => {
        toastSuccess(t('Pending token staked back!'), t('Your are staking for rewards.'))
        setPendingTx(false)
        onDismiss()
      })
      .on('error', (error) => {
        console.error(error)
        toastError(
          t('Could not be staked back'),
          t('There may be an issue with your transaction.'),
        )
        setPendingTx(false)
        onDismiss()
      })
  }
  const title = actionToWithdraw?t('Withdraw'):t('Stake back')
  const actionText = actionToWithdraw?t(`You'll withdraw`):t(`You'll stake back`)
  return (
    <Modal title={title} onDismiss={onDismiss} headerBackground={theme.colors.gradients.cardHeader}>
      {tooltipVisible && tooltip}
      <Flex alignItems="flex-start" justifyContent="space-between">
        <Text>{actionText}</Text>
        <Flex flexDirection="column">
          <Balance bold value={pendingAmount?getBalanceNumber(pendingAmount,stakingToken.decimals):0} decimals={4} unit=" OTO" />
          {/* <Text fontSize="12px" color="textSubtle">
            <Balance
              fontSize="12px"
              color="textSubtle"
              value={0}
              decimals={2}
              unit=" USD"
              prefix="~"
            />
          </Text> */}
        </Flex>
      </Flex>
      <Divider />
      {account ? (
        <Button
          isLoading={pendingTx}
          endIcon={pendingTx ? <AutoRenewIcon spin color="currentColor" /> : null}
          onClick={actionToWithdraw?handleWithdrawClick:handleDepositBackClick}
          mb="28px"
        >
          {t('Confirm')}
        </Button>
      ) : (
        <UnlockButton mb="28px" />
      )}
      <Flex justifyContent="center" alignItems="center">
        <Text fontSize="16px" bold color="textSubtle" mr="4px">
          {t('Rule details')}
        </Text>
        <span ref={targetRef}>
          <HelpIcon color="textSubtle" />
        </span>
      </Flex>
    </Modal>
  )
}

export default WithdrawConfirmModal
